<template>
  <v-container>
    <form>
      <v-layout row wrap v-for="checkups in checkups" :key="checkups.checkupsId" class="mb-3">
        <v-flex xs12 sm10 md8 offset-sm1 offset-md2>
          <v-card class="info">
            <v-container fluid>
              <v-layout row>
                <v-flex xs7 sm8 md9>
                  <v-card-title primary title>
                    <h4 class="white--text mb-0"> {{checkups.stockName.text}}</h4>
                    <v-spacer></v-spacer><div>
                      <div>Date & Time : {{checkups.date}}</div>
                      <div> Vet : {{checkups.vetName.text}}</div>
                    </div>
                  </v-card-title>
                  <v-card-actions align-right>
                    <template>
                      <app-edit-checkup-dialog :checkups="checkups"></app-edit-checkup-dialog>
                    </template>
                  </v-card-actions>
                </v-flex>
              </v-layout>
            </v-container>
          </v-card>
            </v-flex>
          </v-layout>
        </form>
      </v-container>
    </template>

<script>
  export default {
    computed: {
      checkups () {
        return this.$store.getters.loadedCheckups
      }
    }
}
</script>



